<template>
  <div class="Contents">
    <van-tabs v-model="activeTabs"  @change="changes" :sticky="sticky" :color="borderColor" title-inactive-color="#484848" title-active-color="#FF8D59" swipeable>
      <van-tab  title="我的门店" v-if="tabShow2">
        <earningsLeft v-if="divShow"></earningsLeft>
      </van-tab>
      <van-tab title="下级代理商"  v-if="tabShow">
        <earningsRight v-if="!divShow"   ></earningsRight>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
  const earningsLeft = () => import("../../dataStat/Earnings/earningsLeft.vue");
  const earningsRight = () => import("../../dataStat/Earnings/earningsRight.vue");
  export default {
    components:{
      earningsLeft,
      earningsRight
    },
    data() {
      return {
        activeTabs:0,
        divShow:true,
        sticky:true,
        tabShow:false,
        tabShow2:false,
        borderColor:"rgba(0,0,0,0)"
      }
    },
    mounted(){
      if(this.$store.state.accountType=="1"&&parseInt(this.$store.state.childAgentCount)>0){
        this.tabShow=true;
      }
      if(parseInt(this.$store.state.storeCount)>0){

        this.tabShow2=true;

      }else {
        this.divShow=false;
      }

      if(parseInt(this.$store.state.childAgentCount)>0&&parseInt(this.$store.state.storeCount)>0){
        this.borderColor="rgb(255, 141, 89)";
      }
    },
    methods:{
      changes(index){
        this.$store.state.shopId="";
        this.$store.state.subAgentId="";
        if(index==1){
            this.divShow=false;
        }else {
          this.divShow=true;
        }
      }
    },
  }
</script>

<style scoped>

.Contents{
  margin-top: 180px;
}
</style>



